@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap");

*{
    box-sizing: border-box;
}

body{
    background-color: #f5f5f5;
    color: #444;
    font-family: 'Poppins',sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 60vh;
    margin: 0;
}

h1{
    color: rgb(179, 131, 225);
    font-size:4rem;
    text-align: center;
    opacity:0.4;
}

form{
    box-sizing: opx 4px 10px rgba(0,0,0,0.3);
    max-width: 100%;
    width: 100%;
}
.input{
    border: none;
    color: #444;
    font-size: 2rem;
    padding: 0.5rem 1rem;
    display: block;
    width: 100%;
}
.input::placeholder{
    color: #d5d5d5;
}

#todos{
    background-color: #ffff;
    margin: 0;
    padding: 0;
    list-style: none;
}

#todos li{
    border-top: 1px solid #e5e5e5;
    cursor: pointer;
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
}

#todos li.completed{
    color: #b6b6b6;
    text-decoration: line-through;
}
small {
    color: #b5b5b5;
    margin-top: 3rem;
    text-align: center;
  }